<template>
  <main class="main">
    <div class="container">
      <h1 class="main-title">Posts</h1>
      <div class="main__box">
        <div class="main__box-item" v-for="post in allPosts" :key="post.id">
          <h3>Пользователь №{{ post.userId }}</h3>
          <p><span>Пост</span> №{{ post.id }}</p>
          <router-link :to="{name:'postsId', params:{id:post.id}}" class="main__box-btn">Подробнее</router-link>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
    methods:{
        ...mapActions(['getPosts'])
    },
    computed:{
        ...mapState(['allPosts'])
    },
  created() {
    this.getPosts()
    
  },
  

};
</script>

